.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 2s linear;
  }
}

.App-header {
  background-color: #be1506;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header p{
  font-size: 50px;
  color: coral;
}

.btn{
  background-color: coral;
  padding: 20px;
  border: none;
  border-radius: 20px;
  font-size: 20px;
  color: #ffffff;
  margin-bottom: 10px;
}
